export default {
	name: "uploadImage",
	emits:['uploadFiles','choose','delFile'],
	props: {
		filesList: {
			type: Array,
			default () {
				return []
			}
		},
		disabled:{
			type: Boolean,
			default: false
		},
		disablePreview: {
			type: Boolean,
			default: false
		},
		limit: {
			type: [Number, String],
			default: 9
		},
		imageStyles: {
			type: Object,
			default () {
				return {
					width: 'auto',
					height: 'auto',
					border: {}
				}
			}
		},
		delIcon: {
			type: Boolean,
			default: true
		},
		readonly:{
			type:Boolean,
			default:false
		}
	},
	computed: {
		styles() {
			let styles = {
				width: 'auto',
				height: 'auto',
				border: {}
			}
			return Object.assign(styles, this.imageStyles)
		},
		boxStyle() {
			const {
				width = 'auto',
					height = 'auto'
			} = this.styles
			let obj = {}
			if (height === 'auto') {
				if (width !== 'auto') {
					obj.height = this.value2px(width)
					obj['padding-top'] = 0
				} else {
					obj.height = 0
				}
			} else {
				obj.height = this.value2px(height)
				obj['padding-top'] = 0
			}

			if (width === 'auto') {
				if (height !== 'auto') {
					obj.width = this.value2px(height)
				} else {
					obj.width = '33.3%'
				}
			} else {
				obj.width = this.value2px(width)
			}

			let classles = ''
			for(let i in obj){
				classles+= `${i}:${obj[i]};`
			}
			return classles
		},
		borderStyle() {
			let {
				border
			} = this.styles
			let obj = {}
			if (typeof border === 'boolean') {
				obj.border = border ? '1px #eee solid' : 'none'
			} else {
				let width = (border && border.width) || 1
				width = this.value2px(width)
				let radius = (border && border.radius) || 5
				radius = this.value2px(radius)
				obj = {
					'border-width': width,
					'border-style': (border && border.style) || 'solid',
					'border-color': (border && border.color) || '#eee',
					'border-radius': radius
				}
			}
			let classles = ''
			for(let i in obj){
				classles+= `${i}:${obj[i]};`
			}
			return classles
		}
	},
	methods: {
		uploadFiles(item, index) {
			this.$emit("uploadFiles", item)
		},
		choose() {
			this.$emit("choose")
		},
		delFile(index) {
			this.$emit('delFile', index)
		},
		prviewImage(img, index) {
			let urls = []
			if(Number(this.limit) === 1&&this.disablePreview&&!this.disabled){
				this.$emit("choose")
			}
			if(this.disablePreview) return
			this.filesList.forEach(i => {
				urls.push(i.url)
			})

			uni.previewImage({
				urls: urls,
				current: index
			});
		},
		value2px(value) {
			if (typeof value === 'number') {
				value += 'px'
			} else {
				if (value.indexOf('%') === -1) {
					value = value.indexOf('px') !== -1 ? value : value + 'px'
				}
			}
			return value
		}
	}
}
